import _ from 'lodash';
import React, { Component } from 'react';
import SearchResultRoute from './search-result-route';

export default class SearchResult extends Component {

  render() {
    const { search } = this.props;
    const { routes } = search;

    return (
      <div className="result-container">
        <h4 className="count"> : <em>{ routes.resultCount }</em></h4>
        {
          (() => {
            if (_.isEqual(routes.resultCount, 0)) {
                return <p className="no-result">  .</p>;
            }
            return (
              <div className="search-result">
                <div className="search-th">
                  <span className="td sport"></span>
                  <span className="td dport"></span>
                  <span className="td depth"></span>
                  <span className="td cost"></span>
                  <span className="td term"></span>
                  <span className="td map-btn"></span>
                </div>
                <ul className="search-body">
                {
                  _.map(routes.routes, (route, index) => (
                    <SearchResultRoute key={index} route={route} search={search}/>
                  ))
                }
                </ul>
              </div>
            );
          })()
        }

      </div>
    );
  }
}



